 transparent;\r\n  border: none;\r\n  font-size: 18px;\r\n  padding: 8px;\r\n  cursor: pointer;\r\n}\r\n\r\n.app-bar .app-bar__brand {\r\n  display: flex;\r\n  align-items: center;\r\n}\r\n\r\n.app-bar .app-bar__brand h1 {\r\n  color: #db0000;\r\n  text-transform: uppercase;\r\n  font-size: 22px;\r\n  user-select: none;\r\n}\r\n\r\n.app-bar .app-bar__navigation {\r\n  position: absolute;\r\n  top: 50px;\r\n  left: -180px;\r\n  width: 150px;\r\n  transition: all 0.3s;\r\n  padding: 8px;\r\n  background-color: white;\r\n  overflow: hidden;\r\n}\r\n\r\n.app-bar .app-bar__navigation.open {\r\n  left: 0;\r\n}\r\n\r\n.app-bar .app-bar__navigation ul li a {\r\n  display: inline-block;\r\n  text-decoration: none;\r\n  color: black;\r\n  padding: 8px;\r\n  margin-bottom: 5px;\r\n  width: 100%;\r\n}\r\n\r\n/*\r\n  Main Content\r\n*/\r\n\r\nmain {\r\n  padding: 32px;\r\n  flex: 1;\r\n}\r\n\r\n.content {\r\n  margin: 0 auto;\r\n  min-height: 100%;\r\n}\r\n\r\n.content .content__heading {\r\n  font-weight: normal;\r\n}\r\n\r\n/*\r\n  Movies\r\n*/\r\n\r\n.movies {\r\n  display: grid;\r\n  grid-template-columns: 1fr;\r\n  gap: 16px;\r\n  margin: 32px 0;\r\n}\r\n\r\n/*\r\n  Movie Item\r\n*/\r\n\r\n.movie-item {\r\n  width: 100%;\r\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\r\n  border-radius: 8px;\r\n  overflow: hidden;\r\n}\r\n\r\n.movie-item__header {\r\n  position: relative;\r\n}\r\n\r\n.movie-item .movie-item__header .movie-item__header__poster {\r\n  width: 100%;\r\n}\r\n\r\n.movie-item .movie-item__header .movie-item__header__rating {\r\n  position: absolute;\r\n  padding: 8px;\r\n  bottom: 20px;\r\n  left: 0;\r\n  display: inline-block;\r\n  background-color: black;\r\n  color: white;\r\n}\r\n\r\n.movie-item .movie-item__header .movie-item__header__rating .movie-item__header__rating__score {\r\n  margin-left: 10px;\r\n}\r\n\r\n.movie-item .movie-item__content {\r\n  padding: 16px;\r\n}\r\n\r\n.movie-item .movie-item__content h3 {\r\n  margin: 0 0 10px 0;\r\n}\r\n\r\n.movie-item .movie-item__content h3 a {\r\n  color: #db0000;\r\n  text-decoration: none;\r\n}\r\n\r\n.movie-item .movie-item__content p {\r\n  overflow: hidden;\r\n  text-overflow: ellipsis;\r\n  display: -webkit-box;\r\n  -webkit-line-clamp: 4; /* number of lines to show */\r\n  line-clamp: 4;\r\n  -webkit-box-orient: vertical;\r\n}\r\n\r\n/*\r\n  Movie\r\n*/\r\n\r\n.movie {\r\n  margin: 0 auto;\r\n  width: 100%;\r\n  max-width: 800px;\r\n  display: grid;\r\n  grid-template-columns: 1fr;\r\n  gap: 18px 16px;\r\n}\r\n\r\n.movie .movie__poster {\r\n  width: 100%;\r\n  max-width: 400px;\r\n}\r\n\r\n.movie .movie__info h4 {\r\n  margin: 8px 0;\r\n}\r\n\r\n/*\r\n  Footer\r\n*/\r\n\r\nfooter {\r\n  padding: 16px;\r\n}\r\n\r\nfooter #footerTools {\r\n  display: flex;\r\n  flex-direction: column;\r\n  justify-content: center;\r\n  gap: 8px;\r\n}\r\n\r\nfooter p {\r\n  margin-top: 16px;\r\n\r\n  text-align: center;\r\n  color: #aaaaaa;\r\n}\r\n\r\nfooter p a {\r\n  color: #db0000;\r\n  text-decoration: none;\r\n}\r\n\r\n/*\r\n  Like\r\n*/\r\n\r\n.like {\r\n  font-size: 18px;\r\n  position: fixed;\r\n  bottom: 16px;\r\n  right: 16px;\r\n  background-color: #db0000;\r\n  color: white;\r\n  border: 0;\r\n  border-radius: 50%;\r\n  width: 55px;\r\n  height: 55px;\r\n  cursor: pointer;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n}\r\n\r\n/*\r\n  Button\r\n*/\r\n\r\n.btn {\r\n  display: inline-block;\r\n  padding: 12px 24px;\r\n  background-color: #DB0000;\r\n  border: 1px solid #770000;\r\n  border-radius: 4px;\r\n \r\n  font-weight: 500;\r\n  font-size: 16px;\r\n  color: #ffffff;\r\n \r\n  cursor: pointer;\r\n  transition: .15s ease-in-out;\r\n}\r\n\r\n.btn:hover {\r\n  background-color: #DB0000BB;\r\n}\r\n\r\n.btn:active {\r\n  background-color: #DB0000DD;\r\n}\r\n\r\n.btn:focus-visible {\r\n  outline: 3px solid #DB000055;\r\n}\r\n\r\n\r\n/*==================== VARIABLES CSS ====================*/\r\n:root {\r\n  --header-height: 3rem;\r\n\r\n  /*========== Colors ==========*/\r\n  /* Change favorite color */\r\n  --hue-color: 190; /*Purple 250 - Green 142 - Blue 230 - Pink 340*/\r\n\r\n  /* HSL color mode */\r\n  --first-color: hsl(var(--hue-color), 69%, 61%);\r\n  --first-color-second: hsl(var(--hue-color), 69%, 61%);\r\n  --first-color-alt: hsl(var(--hue-color), 57%, 53%);\r\n  --first-color-lighter: hsl(var(--hue-color), 92%, 85%);\r\n  --title-color: hsl(var(--hue-color), 8%, 15%);\r\n  --text-color: hsl(var(--hue-color), 8%, 45%);\r\n  --text-color-light: hsl(var(--hue-color), 8%, 65%);\r\n  --input-color: hsl(var(--hue-color), 70%, 96%);\r\n  --body-color: hsl(var(--hue-color), 60%, 99%);\r\n  --container-color: #fff;\r\n  --scroll-bar-color: hsl(var(--hue-color), 12%, 90%);\r\n  --scroll-thumb-color: hsl(var(--hue-color), 12%, 80%);\r\n\r\n  /*========== Font and typography ==========*/\r\n  --body-font: \"Poppins\", sans-serif;\r\n\r\n  /* .5rem = 8px, 1rem = 16px, 1.5rem = 24px ... */\r\n  --big-font-size: 2rem;\r\n  --h1-font-size: 1.5rem;\r\n  --h2-font-size: 1.25rem;\r\n  --h3-font-size: 1.125rem;\r\n  --normal-font-size: 0.938rem;\r\n  --small-font-size: 0.813rem;\r\n  --smaller-font-size: 0.75rem;\r\n\r\n  /*========== Font weight ==========*/\r\n  --font-medium: 500;\r\n  --font-semi-bold: 600;\r\n\r\n  /*========== Margenes Bottom ==========*/\r\n  /* .25rem = 4px, .5rem = 8px, .75rem = 12px ... */\r\n  --mb-0-25: 0.25rem;\r\n  --mb-0-5: 0.5rem;\r\n  --mb-0-75: 0.75rem;\r\n  --mb-1: 1rem;\r\n  --mb-1-5: 1.5rem;\r\n  --mb-2: 2rem;\r\n  --mb-2-5: 2.5rem;\r\n  --mb-3: 3rem;\r\n\r\n  /*========== z index ==========*/\r\n  --z-tooltip: 10;\r\n  --z-fixed: 100;\r\n  --z-modal: 1000;\r\n}\r\n\r\n/* Font size for large devices */\r\n@media screen and (min-width: 968px) {\r\n  :root {\r\n    --big-font-size: 3rem;\r\n    --h1-font-size: 2.25rem;\r\n    --h2-font-size: 1.5rem;\r\n    --h3-font-size: 1.25rem;\r\n    --normal-font-size: 1rem;\r\n    --small-font-size: 0.875rem;\r\n    --smaller-font-size: 0.813rem;\r\n  }\r\n}\r\n\r\n/*========== Variables Dark theme ==========*/\r\nbody.dark-theme {\r\n  /* HSL color mode */\r\n  --first-color-second: hsl(var(--hue-color), 30%, 8%);\r\n  --title-color: hsl(var(--hue-color), 8%, 95%);\r\n  --text-color: hsl(var(--hue-color), 8%, 45%);\r\n  --text-color-light: hsl(var(--hue-color), 8%, 75%);\r\n  --input-color: hsl(var(--hue-color), 29%, 16%);\r\n  --body-color: hsl(var(--hue-color), 28%, 12%);\r\n  --container-color: hsl(var(--hue-color), 29%, 16%);\r\n  --scroll-bar-color: hsl(var(--hue-color), 12%, 48%);\r\n  --scroll-thumb-color: hsl(var(--hue-color), 12%, 36%);\r\n}\r\n\r\n/*==================== BASE ====================*/\r\n\r\n* {\r\n  box-sizing: border-box;\r\n  padding: 0;\r\n  margin: 0;\r\n}\r\n\r\nhtml {\r\n  scroll-behavior: smooth;\r\n}\r\n\r\nbody {\r\n  margin: 0 0 var(--header-height) 0;\r\n  font-family: var(--body-font);\r\n  font-size: var(--normal-font-size);\r\n  background-color: var(--body-color);\r\n  color: var(--text-color);\r\n}\r\n\r\nh1,\r\nh2,\r\nh3,\r\nh4 {\r\n  color: var(--title-color);\r\n  font-weight: var(--font-semi-bold);\r\n}\r\n\r\nul {\r\n  list-style: none;\r\n}\r\n\r\na {\r\n  text-decoration: none;\r\n}\r\n\r\nimg {\r\n  width: 100%;\r\n  height: auto;\r\n}\r\n\r\n/*==================== REUSABLE CSS CLASSES ====================*/\r\n.section {\r\n  padding: 2rem 0 4rem;\r\n  margin-bottom: 40px;\r\n}\r\n\r\n.section__title {\r\n  font-size: var(--h1-font-size);\r\n}\r\n\r\n.section__subtitle {\r\n  display: block;\r\n  font-size: var(--small-font-size);\r\n  margin-bottom: var(--mb-2);\r\n}\r\n\r\n.section__title,\r\n.section__subtitle {\r\n  text-align: center;\r\n}\r\n\r\n/*==================== LAYOUT ====================*/\r\n\r\n.container {\r\n  max-width: 768px;\r\n  margin-left: var(--mb-1-5);\r\n  margin-right: var(--mb-1-5);\r\n}\r\n\r\n.grid {\r\n  display: grid;\r\n  gap: 1.5rem;\r\n}\r\n\r\n.header {\r\n  width: 100%;\r\n  position: fixed;\r\n  bottom: 0;\