-radius: 0.5rem;\r\n}\r\n\r\n::-webkit-scrollbar-thumb {\r\n  background-color: var(--scroll-thumb-color);\r\n  border-radius: 0.5rem;\r\n}\r\n\r\n::-webkit-scrollbar-thumb:hover {\r\n  background-color: var(--text-color-light);\r\n}\r\n\r\n/*==================== MEDIA QUERIES ====================*/\r\n/* For small devices */\r\n@media screen and (max-width: 380px) {\r\n  .container {\r\n    margin-left: var(--mb-1);\r\n    margin-right: var(--mb-1);\r\n  }\r\n\r\n  .home__content {\r\n    grid-template-columns: 0.35fr 3fr;\r\n  }\r\n\r\n  .home__blob {\r\n    width: 250px;\r\n  }\r\n\r\n  .home__social-icon {\r\n    width: 30px;\r\n  }\r\n\r\n  .home__subtitle {\r\n    font-size: 24px;\r\n  }\r\n\r\n  .home__description {\r\n    font-size: 18px;\r\n  }\r\n}\r\n\r\n/* For medium devices */\r\n@media screen and (min-width: 568px) {\r\n  .home__content {\r\n    grid-template-columns: max-content 1fr 1fr;\r\n  }\r\n\r\n  .home__data {\r\n    grid-column: initial;\r\n  }\r\n  .home__img {\r\n    order: 1;\r\n    justify-self: center;\r\n  }\r\n\r\n  .about__container {\r\n    grid-template-columns: repeat(2, 1fr);\r\n  }\r\n\r\n  @media screen and (min-width: 768px) {\r\n    .container {\r\n      margin-left: auto;\r\n      margin-right: auto;\r\n    }\r\n\r\n    .body {\r\n      margin: 0;\r\n    }\r\n    .section {\r\n      padding: 6rem 0 2rem;\r\n    }\r\n    .section__subtitle {\r\n      margin-bottom: 4rem;\r\n    }\r\n    .header {\r\n      top: 0;\r\n      bottom: initial;\r\n    }\r\n\r\n    .header,\r\n    .main,\r\n    .footer__container {\r\n      padding: 0 1rem;\r\n    }\r\n\r\n    .home__container {\r\n      row-gap: 5rem;\r\n    }\r\n\r\n    .home__content {\r\n      padding-top: 5.5rem;\r\n      column-gap: 2rem;\r\n    }\r\n    .home__blob {\r\n      width: 270px;\r\n    }\r\n    .home__scroll {\r\n      display: block;\r\n    }\r\n    .about__container {\r\n      column-gap: 5rem;\r\n    }\r\n    .about__img {\r\n      width: 350px;\r\n    }\r\n    .a